  color: #151C22; }

.grid-2__item {
  align-self: start; }

.grid-2__item:nth-child(1) {
  grid-row: 1/3; }

.grid-2__item:nth-child(1) {
  grid-row: 1/2; }

.grid-2__item:nth-child(3) {
  grid-row: 3/4; }

.grid-2__item:nth-child(4) {
  grid-row: 2/4; }

.grid-3__item:nth-child(5) {
