@media (min-width: 1200px) {
  .grid {
    grid-template-columns: repeat(4, 1fr); }
  .grid-3__item--big {
    grid-column: 3/5;
    grid-row: 2/4; }
  .grid-3__item:nth-child(6) {
    align-self: normal; }
  .grid-3__item:nth-child(n+8) {
    align-self: end; }
  .grid-2__item:nth-child(n) {
